'use client'

import { type FC, useState } from 'react'
import { useTranslations } from 'next-intl'

// interface
interface IProps {
  onSearch: (query: string) => void
  initialValue?: string
}

// component
const SearchFormComponent: FC<Readonly<IProps>> = (props) => {
  const { onSearch, initialValue = '' } = props

  const [searchQuery, setSearchQuery] = useState(initialValue)
  const t = useTranslations()

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSearch(searchQuery)
  }

  // return
  return (
    <form onSubmit={handleSubmit} className='w-full max-w-md'>
      <div className='flex gap-2'>
        <input
          type='text'
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          placeholder={t('Search.placeholder')}
          className='flex-1 rounded-md border border-gray-300 px-3 py-2 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 focus:outline-none'
        />

        <button
          type='submit'
          className='rounded-md bg-blue-600 px-4 py-2 text-white hover:bg-blue-700 focus:ring-2 focus:ring-blue-200 focus:outline-none'
        >
          {t('Search.button')}
        </button>
      </div>
    </form>
  )
}

export default SearchFormComponent
